#modal {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  #modal-background {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
  }
  
  #modal-content {
    position: absolute;
    z-index:200;
  }
  

  button {
    font-family: 'Fredoka', sans-serif;
    padding: 5px;
    border-radius: 15px;
  }

  button:hover {
    background-color: #73766D;
  }
